import React, { useContext } from 'react'
import styled from 'styled-components';
import { Progress} from 'reactstrap'
import { scoreContext } from '../contexts/StepContext';

const TestResultBlockStyle=styled.div`
padding: 32px;
padding-top: 64px;
font-size: 14px;

display: flex;
flex-direction: column;
align-items: center;

p{
    margin: 0;
    font-size: 20px;
    margin-bottom: 5px;
}
.pg{
    width: 70%;
    height: 10px;
}
h1{
    padding-left: 48px;
    padding-right: 48px;
    font-size: 24px;
    font-weight: bold;
    margin-top: 32px;
    margin-bottom: 48px;
    line-height: 32px;
}
`;

function Title(props){
    if(props.props===0){
        return <h1>!    ?    !</h1>;
    }else if(props.props<=20){
        return <h1>, !       !</h1>;
    }else if(props.props<=40){
        return <h1>   ! ~</h1>;
    }else if(props.props<=60){
        return <h1>  . !</h1>;
    }else if(props.props<=80){
        return <h1>   ?</h1>;
    }else if(props.props<=99){
        return <h1>  10 ! ?</h1>;
    }else if(props.props===100){
        return <h1>!!   ?  !!</h1>;
    }
}

function TestResultBlock() {

    const {score}=useContext(scoreContext);
  return (
    <TestResultBlockStyle>
        <Title props={score} />
        <p> {score}% !</p>
        <Progress className='pg' value={score} color='secondary'/>
    </TestResultBlockStyle>
  )
}

export default TestResultBlock