import { Button } from 'reactstrap';
import React, { useContext } from 'react'
import styled from 'styled-components'
import { wholeStepContext } from '../contexts/StepContext';

const TestIntroBlockStyle=styled.div`
padding: 32px;
padding-top: 64px;
font-size: 14px;

display: flex;
flex-direction: column;
align-items: center;
p{
    border: 1px solid lightgray;
    border-radius: 16px;
    padding: 20px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    margin-bottom: 64px;
}
Button{
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
}
`;


function TestIntroBlock() {
    const {wholeStep,setWholeStep}=useContext(wholeStepContext);
    const onNext=()=>{
     setWholeStep(wholeStep+1);
    }
  return (
    <TestIntroBlockStyle>
    <p>     .<br/>
            .<br/>
                ,
                .<br/>
                  
                 .<br/>
              (      )</p>
    <Button color='secondary' outline onClick={onNext} ></Button>
    </TestIntroBlockStyle>
  )
}

export default TestIntroBlock